import { toast } from "react-toastify";

const onSubmitForm = (e, onClickOpenModal, clearInput) => {
  e.preventDefault();
  let botId = "5248005861:AAG1tkGWe5C8THhXjsy7dKuPbMO5CYBiSc8";
  let chatId = "1285249707";
  let name = e.target[0].value;
  let number = e.target[1].value;
  let course = e.target[2].value;
  let message = `Ismi: ${name}\nTelefon raqami: ${number}\nKurs: ${
    course ? course : "Qiziqish"
  }`;

  (async (url, data) => {
    const res = await fetch(url, {
      method: "POST",
      headers: {
        "Content-type": "application/json",
        "cache-control": "no-cache",
      },
      body: data,
    });
    return await res.json();
  })(
    "https://api.telegram.org/bot" + botId + "/sendMessage",
    JSON.stringify({ chat_id: chatId, text: message })
  )
    .then(() => {
      toast.success(
        "Ma'lumotingiz yuborildi! Tez orada siz bilan bog'lanamiz!",
        {
          position: "top-right",
          autoClose: 5000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
        }
      );
    })
    .catch(() => {
      toast.error(
        "Xato! Iltimos internetingizni tekshirib, boshqatdan urunib ko'ring!",
        {
          position: "top-right",
          autoClose: 5000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
        }
      );
    })
    .finally(() => {
      if (onClickOpenModal) onClickOpenModal();
      if (clearInput) clearInput();
    });
};

export default onSubmitForm;
