import "./Modal.css";
import onSubmitForm from "../Api/sendMessage";
import Cleave from "cleave.js/react";

const Modal = ({ onClickOpenModal }) => {
  return (
    <div>
      <div className="modals">
        <button className="close-modal" onClick={() => onClickOpenModal()}>
          &times;
        </button>
        <form
          className="modal-form"
          onSubmit={(e) => onSubmitForm(e, onClickOpenModal, null)}
        >
          <div>
            <label htmlFor="">
              <input
                type="text"
                name="name"
                placeholder="Ism va familyangiz"
                required
                autoComplete="off"
                minLength={3}
              />
            </label>
          </div>
          <div>
            <label htmlFor="">
              <Cleave
                options={{
                  blocks: [4, 0, 2, 0, 3, 2, 2],
                  delimiters: [" ", "(", ")", " ", "-"],
                  numericOnly: true,
                  prefix: "+998",
                }}
                required
                name="numberInput"
                autoComplete="off"
                type="tel"
              />
            </label>
          </div>
          <div>
            <label htmlFor="">
              <select name="courseSelect" id="">
                <option value="Kompyuter savodxonligi">
                  Kompyuter savodxonligi
                </option>
                <option value="Frontend dasturlash">Frontend dasturlash</option>
                <option value="IT English">IT English</option>
              </select>
            </label>
          </div>
          <div>
            <button type="submit">Yuborish</button>
          </div>
        </form>
      </div>
      <div className="overlay" onClick={() => onClickOpenModal()}></div>
    </div>
  );
};

export default Modal;
